import React, { useContext } from 'react';
import { Redirect, Route } from 'react-router-dom';
import AuthContext from './modules/auth/context/AuthContext';

const RutaPrivada = ({ path, componente: Componente }) => {

    const { autenticado, cargando, usuarioTipo } = useContext(AuthContext);

    // console.log("autenticado", autenticado);
    // console.log("cargando", cargando);
    // console.log("usuarioTipo", usuarioTipo);

    return (
        cargando ?
            <h1 className="display-4">Cargando...</h1> :
            (autenticado && usuarioTipo === 2) ?
                <Route path={path} component={Componente} /> :
                <Redirect to={"/"} />
    )
}

export default RutaPrivada;