import React from 'react';
import ElectorVotacionTablaCongresista from '../components/ElectorVotacionTablaCongresista';

const ElectorVotacionCongresista = ({partidos}) => {
    return (
        <>
            <h2 className="text-center">CONGRESO</h2>

            <div className="table-responsive-sm">
                <ElectorVotacionTablaCongresista partidos={partidos}/>
            </div>
        </>
    )
}

export default ElectorVotacionCongresista;
