import React from 'react';
import ElectorVotacionTablaItemPresidente from '../components/ElectorVotacionTablaItemPresidente';

const ElectorVotacionTablaPresidente = ({ partidos }) => {

    return (
        <table className="table table-bordered">
            <tbody>
                {
                    partidos.map(p => {
                        return <ElectorVotacionTablaItemPresidente key={p.partido_id} p={p} />
                    })
                }
            </tbody>
        </table>
    )
}

export default ElectorVotacionTablaPresidente;
