import React from 'react';
import { Route, Switch } from 'react-router-dom';
import ElectorState from './context/ElectorState';
import RutaPrivada from '../../RutaPrivada'

import ElectorRegistroScreen from './screens/ElectorRegistroScreen'
import ElectorVotacionScreen from './screens/ElectorVotacionScreen';

const ElectorRouter = () => {
    return (
        <ElectorState>
            <Switch>
                <Route path={"/elector/registro"} component={ElectorRegistroScreen} />
                <RutaPrivada path={"/elector/votacion"} componente={ElectorVotacionScreen} />
            </Switch>
        </ElectorState>

    )
}

export default ElectorRouter;
