import { URL_BACKEND } from '../environments/environments';

export const registroVoto = async (idPresidente, idPartido, congresista_1, congresista_2, token) => {
    const peticion = await fetch(`${URL_BACKEND}/voto`, {
        method: "POST",
        body: JSON.stringify({
            voto_presidencial: idPresidente,
            voto_congresal: {
                partido: idPartido,
                primer_congresista: congresista_1,
                segundo_congresista: congresista_2
            }
        }),
        headers: {
            "Content-type": "application/json",
            "Authorization": `Bearer ${token}`
        }
    });

    const data = await peticion.json();

    // agregamos el status a la data
    data.status = peticion.status;
    return data;
}