import React, { useContext, useState } from 'react';
import Swal from 'sweetalert2'

import { loginElector } from '../../../services/authServices';
import AuthContext from '../context/AuthContext';

const AuthLoginScreen = ({ history }) => {

    const { iniciarSesion, usuarioTipo } = useContext(AuthContext);

    const [formulario, setFormulario] = useState({
        dni: "",
        email: ""
    });

    const handleChange = (e) => {
        setFormulario({
            ...formulario,
            [e.target.name]: e.target.value,
        });
    };

    const limpiarFormulario = () => {
        setFormulario({
            dni: "",
            email: ""
        });
    }

    const onSubmit = (e) => {
        e.preventDefault();
        // console.log(formulario.dni);
        // console.log(formulario.email);
        loginElector(formulario.dni, formulario.email).then(data => {
            // console.log(data);
            if (data.ok) {
                // TODO llamar a iniciarSesion del context y enviarle el token
                // console.log("contenido incio sesion", data.content);
                iniciarSesion(data.content);
                // console.log("HOLA, ACA DEBERIA DE SER EL HISTORY PUSH");
                history.push("/elector/votacion");
            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: data.message
                })
            }
        });
    }


    return (
        <div className="container contenedor-login bg-light">
            <div className="row justify-content-md-center h-100">
                <div className="col-lg-6 col-md-6 my-auto">
                    <form className="formulario" onSubmit={onSubmit}>

                        <h2 className="text-center mb-5">LOGIN</h2>

                        <div className="mb-3">
                            <label htmlFor="inputCorreo" className="form-label font-weight-bold">DNI</label>
                            <input
                                type="text"
                                className="form-control"
                                placeholder="DNI"
                                name="dni"
                                value={formulario.dni}
                                onChange={handleChange}
                            />
                        </div>

                        <div className="mb-3">
                            <label htmlFor="inputPassword" className="form-label font-weight-bold">Email</label>
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Email"
                                name="email"
                                value={formulario.email}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="mb-3 row justify-content-between">
                            <div className=" col col-md-5 d-grid gap-2">
                                <button type="submit" className="btn btn-primary btn-block">Aceptar</button>
                            </div>
                            <div className=" col col-md-5 d-grid gap-2">
                                <button type="button" className="btn btn-danger btn-block" onClick={limpiarFormulario}>Cancelar</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default AuthLoginScreen;
