import React, { useState } from 'react';
import Swal from 'sweetalert2';
import './ElectorRegistroScreen.css';

import { registroElector } from '../../../services/electorServices';

const ElectorRegistroScreen = () => {

    const [formulario, setFormulario] = useState({
        dni: "",
        email: ""
    });

    const handleChange = (e) => {
        setFormulario({
            ...formulario,
            [e.target.name]: e.target.value,
        });
    };

    const limpiarFormulario = () => {
        setFormulario({
            dni: "",
            email: ""
        });
    }

    const onSubmit = (e) => {
        e.preventDefault();
        console.log(formulario.dni);
        console.log(formulario.email);
        registroElector(formulario.dni, formulario.email).then(data => {
            if (data.ok) {
                Swal.fire({
                    icon: 'success',
                    title: 'OK',
                    text: data.message
                })
                limpiarFormulario();
            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: data.message
                })
            }
        });
    }


    return (
        <div className="container contenedor-login bg-light">
            <div className="row justify-content-md-center h-100">
                <div className="col-lg-6 col-md-6 my-auto">
                    <form className="formulario" onSubmit={onSubmit}>

                        <h2 className="text-center mb-5">REGISTRO</h2>

                        <div className="mb-3">
                            <label htmlFor="inputCorreo" className="form-label font-weight-bold">DNI</label>
                            <input
                                type="text"
                                className="form-control"
                                placeholder="DNI"
                                name="dni"
                                value={formulario.dni}
                                onChange={handleChange}
                            />
                        </div>

                        <div className="mb-3">
                            <label htmlFor="inputPassword" className="form-label font-weight-bold">Email</label>
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Email"
                                name="email"
                                value={formulario.email}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="mb-3 row justify-content-between">
                            <div className=" col col-md-5 d-grid gap-2">
                                <button type="submit" className="btn btn-primary btn-block">Aceptar</button>
                            </div>
                            <div className=" col col-md-5 d-grid gap-2">
                            <button type="button" className="btn btn-danger btn-block" onClick={limpiarFormulario}>Cancelar</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default ElectorRegistroScreen;
