import React from 'react';
import './GeneralHomeScreen.css';
import { Link } from "react-router-dom";

const GeneralHomeScreen = () => {
    return (

        <>
            <div className="contenedor-imagen-principal"></div>
            <div className="container centrar">
                <div className="justify-content-md-center h-100">
                    <h1 className="text-danger text-center bienvenido">BIENVENIDO A LAS VOTACIONES</h1>
                    <br />
                    <div className="row d-flex justify-content-around">
                        <Link className="col col-md-5 btn btn-success" to={"/auth"}>Login</Link>
                        <Link className="col col-md-5 btn btn-danger" to={"/elector/registro"}>Registro</Link>
                    </div>


                </div>
            </div>
        </>

    )
}

export default GeneralHomeScreen;
