import React, { useContext } from 'react';
import ElectorContext from '../context/ElectorContext';

const ElectorVotacionTablaItemPresidente = ({ p }) => {

    const { seleccionarPresidente, votoPresidencial } = useContext(ElectorContext);


    return (
        <tr className="partido">
            <td className="partido-nombre">{p.partido_nombre}</td>
            <td>
                <button className={`rounded mx-auto d-block logo-partido ${votoPresidencial === p.partido_id ? "seleccionado" : ""}`}
                    style={{ backgroundImage: `url(${p.partido_img_partido})`, backgroundSize: "cover" }}
                    onClick={() => {
                        seleccionarPresidente(p.partido_id)
                    }}
                ></button>
            </td>
            <td>
                <button className={`rounded mx-auto d-block logo-partido ${votoPresidencial === p.partido_id ? "seleccionado" : ""}`}
                    style={{ backgroundImage: `url(${p.partido_img_candidato})`, backgroundSize: "cover" }}
                    onClick={() => {
                        seleccionarPresidente(p.partido_id)
                    }}
                ></button>
            </td>
        </tr>
    )
}

export default ElectorVotacionTablaItemPresidente;
