const AuthReducer = (stateActual, action) => {

    switch (action.type) {
        case "INICIAR_SESION":
            return {
                ...stateActual,
                usuarioDNI: action.data.elector_dni,
                usuarioTipo: action.data.elector_tipo,
                autenticado: true,
                cargando: false,
                token: action.data.token
            }

        case "CERRAR_SESION":
            return {
                ...stateActual,
                usuarioDNI: null,
                usuarioTipo: null,
                autenticado: false,
                cargando: true,
                token: null
            }
    }
};

export default AuthReducer;