import { URL_BACKEND } from '../environments/environments';

export const loginElector = async (dni, email) => {
    const peticion = await fetch(`${URL_BACKEND}/iniciarSesion`, {
        method: "POST",
        body: JSON.stringify({
            dni: dni,
            correo: email,
        }),
        headers: {
            "Content-type": "application/json"
        }
    });

    const data = await peticion.json();

    // agregamos el status a la data
    // data.status = peticion.status;
    return data;
}

