import React, { useContext, useEffect, useState } from 'react';
import Swal from 'sweetalert2';
import ElectorHeader from '../components/ElectorHeader';
import ElectorVotacionPresidente from '../components/ElectorVotacionPresidente';
import ElectorVotacionCongresista from '../components/ElectorVotacionCongresista';

import ElectorContext from '../context/ElectorContext';
import AuthContext from '../../auth/context/AuthContext';

import { listadoPartidos } from '../../../services/partidoServices';
import { registroVoto } from '../../../services/votoServices';

import './ElectorVotacionScreen.css';

const ElectorVotacionScreen = ({ history }) => {

    const [partidos, setPartidos] = useState([]);

    const { votoPresidencial, partido, primerCongresista, segundoCongresista, limpiarVotacion } = useContext(ElectorContext);
    const { token, cerrarSesion } = useContext(AuthContext);

    console.log("TOKEN", token);


    const traerPartidos = async () => {
        listadoPartidos().then((data) => {
            // console.log("DATA CONTENT", data.content);
            setPartidos(data.content);
        })
    }


    const onSubmit = (e) => {
        e.preventDefault();

        registroVoto(votoPresidencial, partido, primerCongresista, segundoCongresista, token).then(data => {
            if (data.ok) {
                Swal.fire({
                    icon: 'success',
                    title: 'OK',
                    text: data.message
                }).then((isConfirmed) => {
                    if (isConfirmed) {
                        limpiarVotacion();
                        cerrarSesion();
                        history.push("/");
                    }
                })


            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: data.message
                })
            }
        });
    }


    useEffect(() => {
        traerPartidos();
        // console.log("LISTADO PARTIDOS 1", partidos);
    }, []);

    return (
        <>
            <ElectorHeader />

            <div className="container">
                <div className="row">
                    <div className="col col-md-5 mt-4">
                        <ElectorVotacionPresidente partidos={partidos} />
                    </div>

                    <div className="col col-md-7 mt-4">
                        <ElectorVotacionCongresista partidos={partidos} />
                    </div>
                </div>

                <div className="mt-5 row justify-content-md-center justify-content-sm-center justify-content-center h-100">
                    <form onSubmit={onSubmit}>
                        <button className="btn btn-success btn-lg" type="submit">PROCESAR VOTACION</button>
                    </form>
                </div>
            </div>
        </>
    )
}

export default ElectorVotacionScreen;
