import React, { useContext, useEffect, useReducer } from 'react';
import ElectorContext from './ElectorContext';
import ElectorReducer from './ElectorReducer';

import AuthContext from '../../auth/context/AuthContext';


const ElectorState = (props) => {

    const { usuarioDNI } = useContext(AuthContext);

    const [state, dispatch] = useReducer(ElectorReducer, {
        votoPresidencial: "",
        electorDNI: usuarioDNI,
        partido: "",
        primerCongresista: "",
        segundoCongresista: ""
    });

    const seleccionarPresidente = (partidoID) => {
        let eleccion = {
            partidoID: partidoID
        }

        dispatch({
            type: "SELECCIONAR_PRESIDENTE",
            data: eleccion,
        });
    }


    const seleccionarPartido = (partidoID) => {
        let eleccion = {
            partidoID: partidoID
        }

        dispatch({
            type: "SELECCIONAR_PARTIDO",
            data: eleccion,
        });
    }

    const ingresoPrimerCongresista = (congresista) => {
        let ingreso = {
            numero: congresista
        }

        dispatch({
            type: "PRIMER_CONGRESISTA",
            data: ingreso,
        });
    }


    const ingresoSegundoCongresista = (congresista) => {
        let ingreso = {
            numero: congresista
        }

        dispatch({
            type: "SEGUNDO_CONGRESISTA",
            data: ingreso,
        });
    }


    const limpiarVotacion = () => {
        dispatch({
            type: "LIMPIAR_VOTACION",
        });
    }


    return (
        <ElectorContext.Provider value={{
            votoPresidencial: state.votoPresidencial,
            electorDNI: state.electorDNI,
            partido: state.partido,
            primerCongresista: state.primerCongresista,
            segundoCongresista: state.segundoCongresista,
            seleccionarPresidente: seleccionarPresidente,
            seleccionarPartido: seleccionarPartido,
            ingresoPrimerCongresista: ingresoPrimerCongresista,
            ingresoSegundoCongresista: ingresoSegundoCongresista,
            limpiarVotacion: limpiarVotacion
        }}>
            {props.children}
        </ElectorContext.Provider>
    )
}

export default ElectorState;
