import { URL_BACKEND } from '../environments/environments';

export const registroElector = async (dni, email) => {
    const peticion = await fetch(`${URL_BACKEND}/registrar`, {
        method: "POST",
        body: JSON.stringify({
            elector_dni: dni,
            elector_email: email,
        }),
        headers: {
            "Content-type": "application/json"
        }
    });

    const data = await peticion.json();

    // agregamos el status a la data
    data.status = peticion.status;
    return data;
}
