import React from 'react';
import ElectorVotacionTablaItemCongresista from '../components/ElectorVotacionTablaItemCongresista';

const ElectorVotacionTablaCongresista = ({ partidos }) => {
    return (
        <table className="table table-bordered">
            <tbody>
                {
                    partidos.map(p => {
                        return <ElectorVotacionTablaItemCongresista key={p.partido_id} p={p} />
                    })
                }
            </tbody>
        </table>
    )
}

export default ElectorVotacionTablaCongresista;
