import React, { useContext } from 'react';
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Nav, Navbar, NavDropdown, Button } from 'react-bootstrap';
import AuthContext from '../../auth/context/AuthContext';

const ElectorHeader = () => {

    const { usuarioDNI, cerrarSesion } = useContext(AuthContext);

    const finalizarSesion = () => {
        cerrarSesion();
    }


    return (
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
            <Navbar.Brand href="#home">VOTACION</Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto">
                    <Nav.Link href="#features">Features</Nav.Link>
                    <Nav.Link href="#pricing">Pricing</Nav.Link>
                    <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown>
                </Nav>
                <Nav>
                    <Navbar.Text className="mr-5">
                        Bienvenido : <span className="font-weight-bold">{usuarioDNI}</span>
                    </Navbar.Text>

                    <Navbar.Text className="mr-2">Cerrar sesion </Navbar.Text>

                    <Link
                        className=""
                        to={"/"}
                        onClick={() => {
                            finalizarSesion()
                        }}
                    >
                        <Navbar.Text className="text-danger"><FontAwesomeIcon icon="power-off" /></Navbar.Text>

                    </Link>

                    {/* <Button className="btn-danger"
                        onClick={() => {
                            finalizarSesion()
                        }}>
                        <FontAwesomeIcon icon="power-off" />
                    </Button> */}
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default ElectorHeader;
