import React, { useEffect, useReducer } from 'react';
import AuthContext from './AuthContext';
import AuthReducer from './AuthReducer';

const AuthState = (props) => {
    const [state, dispatch] = useReducer(AuthReducer, {
        autenticado: false,
        usuarioDNI: false,
        usuarioTipo: null,
        token: null,
        cargando: true,
    });


    const iniciarSesion = (token) => {
        const payload = token.split(".")[1];

        const payloadDesencriptado = window.atob(payload);
        // console.log("payloadDesencriptado", payloadDesencriptado);

        const payloadJSON = JSON.parse(payloadDesencriptado);
        // console.log("payloadJSON", payloadJSON);


        dispatch({
            type: "INICIAR_SESION",
            data: {
                ...payloadJSON,
                token: token,
            },
        });
    }


    const cerrarSesion = () => {
        dispatch({
            type: "CERRAR_SESION",
        });
    };


    return (
        <AuthContext.Provider value={{
            usuarioDNI: state.usuarioDNI,
            usuarioTipo: state.usuarioTipo,
            token: state.token,
            autenticado: state.autenticado,
            cargando: state.cargando,
            iniciarSesion: iniciarSesion,
            cerrarSesion: cerrarSesion,
        }}>
            {props.children}
        </AuthContext.Provider>
    )
}

export default AuthState;