import React, { useContext } from 'react';
import ElectorContext from '../context/ElectorContext';

const ElectorVotacionTablaItemCongresista = ({ p }) => {

    const { partido, seleccionarPartido, ingresoPrimerCongresista, ingresoSegundoCongresista, primerCongresista, segundoCongresista } = useContext(ElectorContext);

    const handleChange1 = (e) => {
        ingresoPrimerCongresista(e.target.value);
    }

    const handleChange2 = (e) => {
        ingresoSegundoCongresista(e.target.value);
    }



    return (
        <tr className="partido">
            <td className="partido-nombre">{p.partido_nombre}</td>
            <td>
                <button className={`rounded mx-auto d-block logo-partido ${partido === p.partido_id ? "seleccionado" : ""}`}
                    style={{ backgroundImage: `url(${p.partido_img_partido})`, backgroundSize: "cover" }}
                    onClick={() => {
                        seleccionarPartido(p.partido_id)
                    }}
                ></button>
            </td>
            <td>
                <input
                    className="congresista-numero mx-auto d-block"
                    type="text"
                    name="congresista_1"
                    value={partido === p.partido_id ? primerCongresista : ""}
                    onChange={handleChange1}
                    onClick={() => {
                        seleccionarPartido(p.partido_id)
                    }}
                />
            </td>
            <td>
                <input
                    className="congresista-numero mx-auto d-block"
                    type="text"
                    name="congresista_2"
                    value={partido === p.partido_id ? segundoCongresista : ""}
                    onChange={handleChange2}
                    onClick={() => {
                        seleccionarPartido(p.partido_id)
                    }}
                />
            </td>
        </tr>
    )
}

export default ElectorVotacionTablaItemCongresista;
