const ElectorReducer = (stateActual, action) => {

    switch (action.type) {
        case "SELECCIONAR_PRESIDENTE":
            return {
                ...stateActual,
                votoPresidencial: action.data.partidoID
            }

        case "SELECCIONAR_PARTIDO":
            return {
                ...stateActual,
                partido: action.data.partidoID
            }

        case "PRIMER_CONGRESISTA":
            return {
                ...stateActual,
                primerCongresista: action.data.numero
            }

        case "SEGUNDO_CONGRESISTA":
            return {
                ...stateActual,
                segundoCongresista: action.data.numero
            }

        case "LIMPIAR_VOTACION":
            return {
                ...stateActual,
                votoPresidencial: "",
                electorDNI: "",
                partido: "",
                primerCongresista: "",
                segundoCongresista: ""
            }
    }
};

export default ElectorReducer;