import React from 'react';
import ElectorVotacionTablaPresidente from '../components/ElectorVotacionTablaPresidente'

const ElectorVotacionPresidente = ({ partidos }) => {
    return (
        <>
            <h2 className="text-center">PRESIDENTE</h2>

            <div className="table-responsive-sm">
                <ElectorVotacionTablaPresidente partidos={partidos} />
            </div>
        </>
    )
}

export default ElectorVotacionPresidente;
